* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: beige;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
}

.calendar {
  display: flex;
  align-items: baseline;
  width: 350px;
  padding: 20px;
}
.calendar-label {
  width: 100px;
  margin-right: 10px;
  font-size: 14px;
  font-weight: 700;
}

.timer-control {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 350px;
  padding: 5px 20px 5px 20px;
  border: 1px dotted darkblue;
}

.buttons-label {
  width: 100px;
  margin-right: 10px;
  font-size: 14px;
}

.control-button {
  background-color: yellowgreen;
  width: 70px;
  padding: 5px;
  border-radius: 4px;
  font-size: 14px;
}

.control-button:disabled {
  opacity: 0.6;
  background-color: palegoldenrod;
  cursor: not-allowed;
}

.control-button:not(:last-child) {
  margin-right: 10px;
}

.timer {
  width: 350px;
  padding: 5px 20px 5px 20px;
  border: 1px dotted darkblue;
}

.field {
  display: flex;
  align-items: baseline;
  justify-content: stretch;
}

.field .value {
  margin-right: 50px;
  flex-basis: 50px;
  font-size: 18px;
  font-weight: 700;
  text-align: right;
  font-variant-numeric: oldstyle-nums;
}

.field .label {
  font-size: 18px;
  font-weight: 200;
}

.date-warning {
  width: 350px;
  background-color: #4d4d4d;
  font-size: 28px;
  font-weight: 700;
  text-align: center;
  color: #ffe500;
}
